import React from "react";

class TodoItem extends React.Component {
  constructor() {
    super();
    this.state = {
      id: 1,
      text: "My Text",
      completed: false
    };
  }
  render() {
    const { id, text, completed } = this.state;
    return (
      <div className="todo-item">
        <input type="checkbox" checked={completed} />
        <label>{text}</label>
      </div>
    );
  }
}

export default TodoItem;
